
* {
  margin: 0;
  padding: 0;
}
body {
  font-family: "Microsoft Yahei", "微软雅黑";
}
ul,
ol {
  list-style: none;
}
img {
  border: none;
  vertical-align: middle;
}
input,
select,
textarea {
  outline: none;
  border: none;
  background: none;
}
textarea {
  resize: none;
}
a {
  text-decoration: none;
}
em,
strong,
i,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-style: normal;
  font-weight: normal;
}
.detail {
  position: fixed;
  z-index: 9;
  top: 0;
  left: 0;
  right: 0;
  bottom: 1.33333333rem;
  overflow: hidden;
  background: #f3f5f7;
  font-size: 0.37333333rem;
}
.detail .back {
  position: absolute;
  left: 0.06666667rem;
  top: 0.06666667rem;
  font-size: 0.66666667rem;
  color: #fff;
}
.detail > div > img {
  width: 10rem;
  height: 10rem;
}
.detail .con {
  background: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 0.48rem 0.48rem 0.48rem 0.48rem;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.detail .con h3 {
  color: #07111b;
  font-weight: bold;
}
.detail .con span,
.detail .con p {
  color: #93999f;
  font-size: 0.26666667rem;
}
.detail .con p {
  color: #f01414;
}
.detail .con p strong {
  font-size: 0.37333333rem;
}
.detail .con p span {
  color: #93999f;
  text-decoration: line-through;
  margin-left: 0.32rem;
}
.detail .con .addtocart {
  -ms-flex-item-align: end;
      align-self: flex-end;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 1.97333333rem;
  height: 0.64rem;
  font-size: 0.26666667rem;
  color: #fff;
  padding: 0.16rem 0.32rem 0.16rem 0.32rem;
  background: #00a0dc;
  border-radius: 0.48rem;
}
.detail .info {
  margin-top: 0.42666667rem;
  margin-bottom: 0.42666667rem;
  padding: 0.48rem 0.48rem 0.48rem 0.48rem;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  background: #fff;
}
.detail .info p {
  font-size: 0.32rem;
  margin-left: 0.21333333rem;
  color: #4d555d;
  line-height: 0.64rem;
}
.detail .commonts {
  background: #fff;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.detail .commonts h3 {
  padding: 0.48rem 0.48rem 0.48rem 0.48rem;
}
.detail .commonts .all {
  padding: 0rem 0.48rem 0rem 0.48rem;
}
.detail .commonts .all .btn {
  padding: 0rem 0rem 0.48rem 0rem;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.detail .commonts .all .btn span {
  padding: 0.21333333rem 0.32rem 0.21333333rem 0.32rem;
  color: #4d555d;
  margin-right: 0.21333333rem;
}
.detail .commonts .all .btn span:nth-child(1) {
  background: #00a0dc;
  color: #fff;
}
.detail .commonts .all .btn span:nth-child(2) {
  background: rgba(0, 160, 220, 0.2);
}
.detail .commonts .all .btn span:nth-child(3) {
  background: rgba(77, 85, 93, 0.2);
}
.detail .commonts .all .btn span em {
  font-size: 0.21333333rem;
  margin-left: 0.04rem;
}
.detail .commonts .all .select {
  margin-top: 0.32rem;
  margin-bottom: 0.32rem;
  font-size: 0.48rem;
  color: #93999f;
}
.detail .commonts .all .select i {
  font-size: 0.64rem;
  vertical-align: middle;
  margin-right: 0.10666667rem;
}
.detail .commonts .all .select i.cur {
  color: #00a0dc;
}
.detail .commonts .texts {
  padding: 0rem 0.48rem 0rem 0.48rem;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
}
.detail .commonts .texts li {
  padding: 0.42666667rem 0rem 0.42666667rem 0rem;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.detail .commonts .texts li:last-child {
  border: none;
}
.detail .commonts .texts .datas {
  font-size: 0.26666667rem;
  margin-bottom: 0.16rem;
}
.detail .commonts .texts .datas time {
  color: #93999f;
}
.detail .commonts .texts .datas span {
  float: right;
}
.detail .commonts .texts .text i {
  margin-right: 0.10666667rem;
}
.detail .commonts .texts .text i.icon-down {
  vertical-align: middle;
  color: #93999f;
}
.detail .commonts .texts .text i.icon-damuzhi {
  vertical-align: top;
  color: #00a0dc;
}



