
* {
  margin: 0;
  padding: 0;
}
body {
  font-family: "Microsoft Yahei", "微软雅黑";
}
ul,
ol {
  list-style: none;
}
img {
  border: none;
  vertical-align: middle;
}
input,
select,
textarea {
  outline: none;
  border: none;
  background: none;
}
textarea {
  resize: none;
}
a {
  text-decoration: none;
}
em,
strong,
i,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-style: normal;
  font-weight: normal;
}
.mark-enter,
.mark-leave-active {
  opacity: 0;
}
.mark-enter-active,
.mark-leave-active {
  transition: all 0.5s;
}
.incart-enter,
.incart-leave-active {
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}
.incart-enter-active,
.incart-leave-active {
  transition: all 0.5s;
}
.slide-enter,
.slide-leave-active {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  opacity: 0;
}
.slide-enter-active,
.slide-leave-active {
  transition: all 0.5s;
}
.goods {
  background: #f3f5f7;
  border-top: 1px solid rgba(7, 17, 27, 0.2);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  top: 4.65333333rem;
  bottom: 1.33333333rem;
  overflow: hidden;
}
.goods .names {
  width: 2.4rem;
  font-size: 0.32rem;
  color: #666;
  font-weight: 200;
  text-align: left;
}
.goods .names li {
  height: 1.44rem;
  padding: 0rem 0.32rem 0rem 0.32rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.goods .names li i {
  display: inline-block;
  width: 0.32rem;
  height: 0.32rem;
  background: url(special_3@2x.png);
  background-size: cover;
  vertical-align: middle;
}
.goods .names li.cur {
  background: #fff;
}
.goods .items {
  width: 7.86666667rem;
  font-size: 0.32rem;
  color: #93999f;
}
.goods .items > ul {
  background: #fff;
}
.goods .items h2 {
  font-size: 0.32rem;
  line-height: 0.69333333rem;
  border-left: 2px solid #d9dde1;
  padding: 0rem 0rem 0rem 0.37333333rem;
  background: #f3f5f7;
}
.goods .items .item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 0rem 0.48rem 0rem 0.48rem;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  padding: 0.48rem 0rem 0.48rem 0rem;
}
.goods .items .item:last-child {
  border: none;
}
.goods .items .item img {
  width: 1.33333333rem;
  height: 1.33333333rem;
  height: auto;
  float: left;
  margin-right: 0.26666667rem;
}
.goods .items .item .con {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.goods .items .item .con h3 {
  font-size: 0.37333333rem;
  color: #07111b;
}
.goods .items .item .con p,
.goods .items .item .con span {
  font-size: 0.26666667rem;
  color: #93999f;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 4rem;
}
.goods .items .item .con i {
  color: #f7353b;
  font-size: 0.37333333rem;
  font-weight: bold;
}
.goods .items .item .con i strong {
  font-size: 0.26666667rem;
  font-weight: normal;
}
.goods .items .item .con i span {
  text-decoration: line-through;
  margin-left: 0.21333333rem;
}
.goods .items .item .addtocart {
  -ms-flex-item-align: end;
      align-self: flex-end;
  top: 0.2rem;
}
.cartmark {
  position: fixed;
  width: 100%;
  top: 0;
  bottom: 0;
  z-index: 8;
  background: rgba(7, 17, 27, 0.8);
}
.ball {
  position: fixed;
  left: 0.85333333rem;
  bottom: 0.58666667rem;
  transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
}
.ball .inner {
  width: 0.4rem;
  height: 0.4rem;
  transition: all 0.4s linear;
  border-radius: 50%;
  z-index: 200;
  background: #00a0dc;
}



