
* {
  margin: 0;
  padding: 0;
}
body {
  font-family: "Microsoft Yahei", "微软雅黑";
}
ul,
ol {
  list-style: none;
}
img {
  border: none;
  vertical-align: middle;
}
input,
select,
textarea {
  outline: none;
  border: none;
  background: none;
}
textarea {
  resize: none;
}
a {
  text-decoration: none;
}
em,
strong,
i,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-style: normal;
  font-weight: normal;
}
.cart {
  position: fixed;
  bottom: 0;
  height: 1.33333333rem;
  width: 100%;
  z-index: 10;
  background: #141d27;
}
.cart .bigcir {
  position: absolute;
  left: 0.29333333rem;
  width: 1.54666667rem;
  bottom: 0;
  height: 1.54666667rem;
  border-radius: 50%;
  background: #141d27;
}
.cart .bigcir .smcir {
  position: absolute;
  left: 0.18666667rem;
  width: 1.17333333rem;
  bottom: 0.18666667rem;
  height: 1.17333333rem;
  font-size: 0.64rem;
  color: rgba(255, 255, 255, 0.4);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.1);
}
.cart .bigcir .smcir .num {
  position: absolute;
  font-size: 0.24rem;
  color: #fff;
  right: -0.16rem;
  top: -0.21333333rem;
  padding: 0.02666667rem 0.16rem 0.05333333rem 0.16rem;
  border-radius: 13.32rem;
  background: #f01414;
}
.cart .bigcir .smcir.buy {
  background: #00a0dc;
}
.cart .bigcir .smcir.buy i {
  color: #fff;
}
.cart .con {
  margin-left: 2.13333333rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 0.42666667rem;
  line-height: 1.33333333rem;
  color: rgba(255, 255, 255, 0.4);
}
.cart .con .line {
  border-left: 1px solid rgba(255, 255, 255, 0.1);
  height: 0.66666667rem;
}
.cart .con .price {
  width: 1.33333333rem;
}
.cart .con .price,
.cart .con .pay {
  font-weight: bold;
}
.cart .con .pay {
  text-align: center;
  width: 2.8rem;
  background: rgba(255, 255, 255, 0.1);
}
.cart .con .pay.buy {
  color: #fff;
  background: #00a0dc;
}



