
* {
  margin: 0;
  padding: 0;
}
body {
  font-family: "Microsoft Yahei", "微软雅黑";
}
ul,
ol {
  list-style: none;
}
img {
  border: none;
  vertical-align: middle;
}
input,
select,
textarea {
  outline: none;
  border: none;
  background: none;
}
textarea {
  resize: none;
}
a {
  text-decoration: none;
}
em,
strong,
i,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-style: normal;
  font-weight: normal;
}
html {
  font-size: 37.5px;
}
.routers {
  height: 1.06666667rem;
  line-height: 1.06666667rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.routers a {
  font-size: 0.37333333rem;
  color: #4d555d;
}
.routers a.router-link-active {
  color: #f01414;
}



